.services-card {
    display: flex;
    flex-direction: column;
    flex-basis: 25%;
    width: 290px;
    box-shadow: 0 0 12px 8px #DDDDDD;
    border-radius: 20px;
    margin: 10px;
}

.services-card__img {
    display: flex;
    border-radius: 20px 20px 0 0;
    margin-bottom: 18px;
    max-width: 100%;
    height: auto;
    max-height: 100px;
}

.services-card__info {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    padding: 0 20px 10px;
}

.services-card__info h6 {
    font-size: 18px;
    margin: 0;
}

.services-card__info p {
    font-size: 16px;
    margin: 10px 0 0;
}

.services-card__info-title {
    flex-grow: 1;
}

.services-card__btn {
    padding: 15px;
    display: flex;
    justify-content: center;
}
